<template>
  <div id="app">
  <el-container>
    <el-dialog class='login-page' :visible="true" :fullscreen="true" :show-close="false">
    <el-card class="box-card" style="width: 400px;" >
      <div>
        <h3 style="text-align: center; color: white;"></h3>
        <el-form :model="loginData" ref="loginData">
          <el-form-item v-show="false">
            <el-input v-model="loginData.action"></el-input>
          </el-form-item>
          <el-form-item prop="username" :rules="[{ required: true, message: 'ID'}]">
            <el-input v-model="loginData.username" prefix-icon='el-icon-star-on' placeholder="ID"></el-input>
          </el-form-item>
          <el-form-item prop="password" :rules="[{ required: true, message: ''}]">
            <el-input type="password" v-model="loginData.password" prefix-icon='el-icon-view'  placeholder="" @keyup.enter.native="loginSubmit"></el-input>
          </el-form-item>
          <el-row>
            <el-col :span="12" align='left'>
              <el-checkbox v-model="loginData.checked" style="color: white"></el-checkbox>
            </el-col>
            <el-col :span="12" align='right'>
              <el-button type="primary" @click="loginSubmit" :loading="isloading"> </el-button>
            </el-col>
          </el-row>
        </el-form>
      </div>
    </el-card>
    </el-dialog>
  </el-container>
  </div>
</template>
<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
data() {
    return {
      loginData: {
        username: null,
        password: null,
        checked: false,
        action: "login"
      },
      isloading: false,
      nextPage: '/',
      host: window.location.href.split("/login")[0]
    }
  },
methods: {
  resetForm () {
    this.loginData = {
      username: null,
      password: null,
      checked: false,
      action: "login"
    }
  },

  loginSubmit () {
    if (window.location.href.includes("?next=")){
      this.nextPage = window.location.href.split("?next=")[1];
    }

    let data = this.loginData;
    let config = {
      baseURL: '/proxy/api/v1',
      paramSerializerJQLikeEnabled: true,
      url: '/login',
      method: 'post',
      data: data
    };
    this.isloading = true;
    service.request(config)
    .then(data => {
      console.log(data);
      if (data.result == 'LOGIN_SUCCESS') {
        this.$message({
          message: '',
          type: 'success'
        });
        window.location.href= this.host + this.nextPage;
      } else {
        this.$message({
          message: `${data.message}`,
          type: 'error'
        });
        this.isloading = false;
      }
    }, err => {
      this.$alert(data.message);
      this.isloading = false;
    });

  },

  }
}
</script>

<style>
.login-page .el-dialog{
  background-color: #006699;
  background-image: url('/static/img/nlb_background.jpeg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.box-card{
  margin: 100px auto 0;
  background-color: #cacaca12;
}
</style>



